@charset 'utf-8';
body,button,dd,dl,dt,fieldset,form,h1,h2,h3,h4,h5,h6,input,legend,li,ol,p,select,table,td,textarea,th,ul,figure {
    margin: 0;
    padding: 0;
    font: inherit; }
html {
    height: 100%; }
body {
    height: 100%;
    -webkit-text-size-adjust: none; }
ul,ol,dl,li,dt,dd {
    list-style: none; }
a {
    text-decoration: none; }
address {
    font-style: unset; }

strong,em {
    font-weight: normal;
    font-style: inherit;
    text-decoration: none; }

button {
    border: none;
    outline: none; }

.blind {
    position: absolute;
    overflow: hidden;
    clip: rect(0 0 0 0);
    margin: -1px;
    width: 1px;
    height: 1px; }

